import { Component } from '@angular/core';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { MatDialog } from '@angular/material/dialog';
import { AddAnimeComponent } from '../modals/add-anime/add-anime.component';
import { Animes } from '../models/Animes';
import { AnimeServiceService } from '../services/anime-service/anime-service.service';
import { TimeoutExceptionComponent } from '../modals/timeout-exception/timeout-exception.component';

@Component({
  selector: 'app-root-nav',
  templateUrl: './root-nav.component.html',
  styleUrls: ['./root-nav.component.scss']
})
export class RootNavComponent {

  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches),
      shareReplay()
    );

  constructor(private httpAnime: AnimeServiceService,private breakpointObserver: BreakpointObserver, public dialog: MatDialog) {
    console.log(this.isHandset$)
  }
  public AddAnime(){
    const dialogRef = this.dialog.open(AddAnimeComponent, {
      width: '250px',
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(async result => {
      if(result != undefined){
        let anime: Animes = result.anime;
        try{
          await this.httpAnime.postAnime(anime);  
        }catch(error){
          if(error.name == "TimeoutError"){
            const dialogRef = this.dialog.open(TimeoutExceptionComponent, {
              width: '250px',
              disableClose: true,
            });
          }
        }
      }
    });
  }

}
