import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Downloads } from 'src/app/models/Downloads';

const TIMEOUT = 25000;

@Injectable({
  providedIn: 'root'
})
export class DownloadsService {

  private REST_API_SERVER = "https://anime-express.glitch.me/";

  constructor(private httpClient: HttpClient) { }

  public async getNumberEpisodes(body: any): Promise<any>{
    let promise = await this.httpClient.get<any>(`${this.REST_API_SERVER}countEpisodesById/${body}`).toPromise();
    return promise;
  }

  public async getAllEpisodesByAnimeId(id:string): Promise<Downloads[]>{
    let promise = await this.httpClient.get<Downloads[]>(`${this.REST_API_SERVER}animeEpisodes/${id}`).toPromise();
    return promise;
  }

  public async getEpisodeByNumberAndId(anime_id:string, episode:string) : Promise<Downloads>{
    return await this.httpClient.get<Downloads>(`${this.REST_API_SERVER}getEpisodeByNumberAndId/${anime_id}/${episode}`).toPromise();
  }
  
}