

export class Animes{
    public _id?:string | null;
    public name: string;
    public description:string;
    public urls:string;
    public image:string;
    public brief_desc:string;
    public show_more:boolean = true;
    public total_episodes:number

    constructor(_name:string, _description:string, _urls:string, _image:string, _id?:string | null){
        this.name = _name;
        this.urls = _urls;
        this.image = _image;
        this._id = _id;
        this.description = _description;
    }

}