import { Component, OnInit } from '@angular/core';
import { AnimeServiceService } from '../services/anime-service/anime-service.service';
import { Animes } from '../models/Animes';
import { MatDialog } from '@angular/material/dialog';
import { AddAnimeComponent } from '../modals/add-anime/add-anime.component';
import { DownloadsService } from '../services/downloads-service/downloads.service';
import { Router, ActivatedRoute } from '@angular/router';
import { TimeoutExceptionComponent } from '../modals/timeout-exception/timeout-exception.component';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';


@Component({
  selector: 'app-anime-view',
  templateUrl: './anime-view.component.html',
  styleUrls: ['./anime-view.component.scss']
})
export class AnimeViewComponent implements OnInit {
  public animes:Animes[];
  message: string = "";
  pageSize = 5;
  pageOfItems: Array<Animes>;
  config: any;
  collection = { count: 60, data: [] };
  public selectedVal: string;
  public firstEntryInterval: boolean;

  myControl = new FormControl();
  options: Animes[] = [];
  filteredOptions: Observable<Animes[]>;

  constructor(private httpAnime: AnimeServiceService, public dialog: MatDialog, private httpEpisodes: DownloadsService,
    private route: ActivatedRoute,
    private router: Router) { }

  public async ngOnInit() {
    this.firstEntryInterval = false;
    this.initInterval();
    this.loadAnimes();
    this.selectedVal = "cards";
    this.filteredOptions = this.myControl.valueChanges.pipe(
      startWith(''),
      map(state => state ? this._filter(state) : this.options.slice())
    );
  }

  private initInterval(){
    setInterval(async () => {
      try{
        let animesRefreshed = await this.httpAnime.getAllAnimes();
        if(animesRefreshed.length != this.animes.length){
          this.loadAnimes();
        }
      } catch(error){
        if(error.name == "TimeoutError" && !this.firstEntryInterval){
          this.firstEntryInterval = true;
          const dialogRef = this.dialog.open(TimeoutExceptionComponent, {
            width: '250px',
            disableClose: true
          });
        }
      }
    }, 60000);
  }
  public changeSelection(selected:string){
    
    this.selectedVal = selected;
    if(this.selectedVal == 'cards'){
      this.config = {
        itemsPerPage: 5,
        currentPage: 1,
        totalItems: this.collection.count
      };
    } else {
      this.config = {
        itemsPerPage: 7,
        currentPage: 1,
        totalItems: this.collection.count
      };
    }
  }

  private async loadAnimes(){
    this.message = "Cargando todos los Animes";
    try{
      this.animes = await this.httpAnime.getAllAnimes();
      this.options = this.animes;
      if(this.animes != null){
        this.collection.data = this.animes;
        this.collection.count = this.collection.data.length;
        this.config = {
          itemsPerPage: 5,
          currentPage: 1,
          totalItems: this.collection.count
        };
        this.animes.forEach(async (x) => {
            x.brief_desc = x.description.substr(0,120)+"...";
            if(x.description.length < 120){
              x.brief_desc = x.description;
              x.show_more = false;
            }
            let value = await this.httpEpisodes.getNumberEpisodes(x._id);
            x.total_episodes = value.length;
        });
      }
    } catch(error){
      if(error.name == "TimeoutError"){
        const dialogRef = this.dialog.open(TimeoutExceptionComponent, {
          width: '250px',
          disableClose: true
        });
        this.firstEntryInterval = true;
      }
    }
  }

  private _filter(value: string): Animes[] {
    const filterValue = value.toLowerCase();

    return this.options.filter(anime => anime.name.toLowerCase().indexOf(filterValue) === 0);
  }

  public showMore(anime:Animes){
    anime.show_more = !anime.show_more;
  }
  public viewLinks(id:string){
    this.router.navigate(['/anime-detail', id]);
  }
  public deleteAnime(id:string){
    this.httpAnime.deleteAnime(id).then((x) => {
      this.loadAnimes();
    }).catch((error) => {
      if(error.name == "TimeoutError"){
        const dialogRef = this.dialog.open(TimeoutExceptionComponent, {
          width: '250px',
          disableClose: true
        });
      }
    })
  }
  public AddAnime(){
      const dialogRef = this.dialog.open(AddAnimeComponent, {
        width: '250px',
        disableClose: true
      });
  
      dialogRef.afterClosed().subscribe(result => {
        if(result != undefined){
          let anime: Animes = result.anime;
          this.animes = null;
          try{
            this.httpAnime.postAnime(anime).then((x) => {
              this.loadAnimes();
            });
          } catch(error) {
            if(error.name == "TimeoutError"){
              const dialogRef = this.dialog.open(TimeoutExceptionComponent, {
                width: '250px',
                disableClose: true
              });
            }
          }
        }
      });
  }

  public pageChanged(event){
    this.config.currentPage = event;
  }

  public onValChange(val: string) {
    this.selectedVal = val;
  }

}
