import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AnimeViewComponent } from './anime-view/anime-view.component';
import { AnimeDetailComponent } from './anime-detail/anime-detail.component';
import { AnimeVideComponent } from './anime-vide/anime-vide.component';

const routes: Routes = [{
  path: '',
  component: AnimeViewComponent,
},
{
  path: 'anime-detail/:anime-id',
  component: AnimeDetailComponent
},
{
  path: 'video/:link',
  component: AnimeVideComponent
}];

@NgModule({
  imports: [RouterModule.forRoot(routes, { onSameUrlNavigation: 'reload', useHash:true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
