import { Component, OnInit, AfterContentChecked } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DownloadsService } from '../services/downloads-service/downloads.service';
import { Downloads } from '../models/Downloads';
import { AnimeServiceService } from '../services/anime-service/anime-service.service';
import { Animes } from '../models/Animes';
import * as $ from 'jquery';
import { Clipboard } from '@angular/cdk/clipboard';
import { MatSnackBar } from '@angular/material/snack-bar';
import { TimeoutExceptionComponent } from '../modals/timeout-exception/timeout-exception.component';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-anime-detail',
  templateUrl: './anime-detail.component.html',
  styleUrls: ['./anime-detail.component.scss']
})
export class AnimeDetailComponent implements OnInit, AfterContentChecked {
  id: string;
  private sub: any;
  public episodes: Downloads[];
  public anime: Animes;
  public selectedOptions: string[];

  constructor(private _snackBar: MatSnackBar, public dialog: MatDialog, private clipboard: Clipboard, private route: ActivatedRoute, private httpAnimes: AnimeServiceService, private httpDownloads: DownloadsService, private router: Router) { }
  ngAfterContentChecked(): void {
    let isDefined = $('.lateral-anime-detail')[0] != null;
    if (isDefined) {
      let lateral = $('.lateral-anime-detail').height();
      let top = $('.action-buttons').height();
      let toSet = lateral - top;
      $('.matselection').css('max-height', `${toSet}px`);
    }
  }

  async ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.id = params['anime-id']; // (+) converts string 'id' to a number
    });
    try {
      this.episodes = await this.getAllEpisodesFromAnime();
      this.anime = await this.httpAnimes.getAnime(this.id);
      this.anime = new Animes(this.anime.name, this.anime.description, this.anime.urls, this.anime.image, this.anime._id);
    } catch (error) {
      if (error.name == "TimeoutError") {
        const dialogRef = this.dialog.open(TimeoutExceptionComponent, {
          width: '250px',
          disableClose: true,
          data: { from: 'anime-detail', id: this.id }
        });
      }
    }
  }
  private async getAllEpisodesFromAnime(): Promise<Downloads[]> {
    let episodes = await this.httpDownloads.getAllEpisodesByAnimeId(this.id);
    episodes.sort((a: Downloads, b: Downloads) => {
      return a.episode - b.episode;
    });
    return episodes;
  }
  public goBack() {
    this.router.navigate(['']);
  }
  public async viewMegaFrame(link, episode, anime_id) {
    let max_episodes = await this.httpDownloads.getNumberEpisodes(anime_id);
    console.log(max_episodes);
    let id_mega = link.split('/')[3];
    localStorage.setItem('max_episodes', max_episodes.length + '');
    localStorage.setItem('current_episode', episode);
    localStorage.setItem('anime_id', anime_id + '');
    this.router.navigate(['/video', id_mega])
  }

  public copiarCortapapeles() {
    let string_to_copy = '';
    this.selectedOptions.forEach((x) => {
      string_to_copy += x + " ";
    });
    this.clipboard.copy(string_to_copy);
    this._snackBar.open('LINKS MEGA COPIADOS', 'OK', {
      duration: 5000
    });
    this.unselectAll();
  }
  public onNgModelChange(evt: string[]) {
    if (evt.length > 0) {
      $('#boton_copiar').show();
    } else {
      $('#boton_copiar').hide();
    }
  }

  public unselectAll() {
    let element = $('.mat-pseudo-checkbox');
    element.each((x: any) => {
      if (element[x].className.includes('mat-pseudo-checkbox-checked')) {
        element[x].click();
      }
    });
  }

  public selectAll() {
    let element = $('.mat-pseudo-checkbox');
    element.each((x: any) => {
      if (!element[x].className.includes('mat-pseudo-checkbox-checked')) {
        element[x].click();
      }
    });
  }

}
