import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Animes } from 'src/app/models/Animes';
import { timeout } from 'rxjs/operators';

const TIMEOUT = 25000;

@Injectable({
  providedIn: 'root'
})

export class AnimeServiceService {

  private REST_API_SERVER = "https://anime-express.glitch.me/";
  constructor(private httpClient: HttpClient) { }

  public async getAllAnimes(): Promise<Animes[]>{
    let promise = await this.httpClient.get<Animes[]>(`${this.REST_API_SERVER}allAnime`).pipe(timeout(TIMEOUT)).toPromise();
    return promise;
  }

  public async postAnime(body: any): Promise<any>{
    let myHeaders = new HttpHeaders();
    myHeaders.set('Content-Type', 'application/json');
    let animeCasted = JSON.stringify(body, (key, value) => {
      if (value !== null && key != "show_more") return value
    });
    let obj = JSON.parse(animeCasted);

    return await this.httpClient.post(`${this.REST_API_SERVER}anime`, obj, {headers: myHeaders}).pipe(timeout(TIMEOUT)).toPromise();
  }

  public async getAnime(body: any): Promise<Animes>{
    return await this.httpClient.get<Animes>(`${this.REST_API_SERVER}animeById/${body}`).pipe(timeout(TIMEOUT)).toPromise();
  }

  public async deleteAnime(body:any): Promise<any>{
    return await this.httpClient.delete<any>(`${this.REST_API_SERVER}deleteAnime/${body}`).pipe(timeout(TIMEOUT)).toPromise();
  }
}