import { Component, OnInit } from '@angular/core';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import {Location} from '@angular/common';
import { DownloadsService } from '../services/downloads-service/downloads.service';
import { Downloads } from '../models/Downloads';


@Component({
  selector: 'app-anime-vide',
  templateUrl: './anime-vide.component.html',
  styleUrls: ['./anime-vide.component.scss']
})
export class AnimeVideComponent implements OnInit {
  public megaLink:SafeUrl;
  public anime_id: string;
  public max_episodes:number;
  public current_episodes: number;
  constructor(private route: ActivatedRoute, private httpDownloads: DownloadsService ,private router: Router,private sanitizer: DomSanitizer, private _location: Location) {
    this.router.routeReuseStrategy.shouldReuseRoute = function() {
        return false;
    };
  }

  ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.megaLink = this.sanitizer.bypassSecurityTrustResourceUrl('https://mega.nz/embed' + params['link']);
   });
   this.anime_id = localStorage.getItem('anime_id');
   this.max_episodes = +localStorage.getItem('max_episodes');
   this.current_episodes = +localStorage.getItem('current_episode');
  }

  public goBack(){
    this.router.navigate(['anime-detail', this.anime_id]);
  }

  public hasPreviousEpisode(){
    return (this.current_episodes == 1);
  }

  public hasNextEpisode(){
    return (this.current_episodes < this.max_episodes);
  }

  public async goToPreviousEpisode(){
    this.current_episodes--;
    let episode:Downloads = await this.httpDownloads.getEpisodeByNumberAndId(this.anime_id, this.current_episodes+"");
    localStorage.setItem('current_episode', this.current_episodes+'');
    this.router.navigate(['/video', episode.link.split('/')[3]]);
  }

  public async goToNextEpisode(){
    this.current_episodes++;
    let episode:Downloads = await this.httpDownloads.getEpisodeByNumberAndId(this.anime_id, this.current_episodes+"");
    localStorage.setItem('current_episode', this.current_episodes+'');
    this.router.navigate(['/video', episode.link.split('/')[3]]);
  }

}
